import React, { useEffect, useState } from 'react'
import { CustomTabs } from '../reuseableComponents';
import CorrectionSetting from './correctionSetting/CorrectionSetting';
import AddUpdateCorrectionSetting from './correctionSetting/AddUpdateCorrectionSetting';
import { useDispatch, useSelector } from 'react-redux';
import { clientActions } from '../../core/resource/actions';
import { useCustomTranslation } from '../../translation';
import ApproveRejectModal from '../modal/ApproveRejectModal';
import { activeTabPermission } from '../../utils/common';

const Setting = () => {
    const dispatch = useDispatch()
    const { t } = useCustomTranslation()
    const {
        addClientPending,
        addClientRes,
        addClientError,

        getAllClientPending,
        getAllClientRes,
        getAllClientError,

        updateClientPending,
        updateClientRes,
        updateClientError,

        deleteClientPending,
        deleteClientRes,
        deleteClientError,
    } = useSelector(state => state.client)
    const [isOpenCorrectionSetting, setIsOpenCorrectionSetting] = useState(false)
    const [selectedCorrectionSetting, setSelectCorrectionSetting] = useState('')
    const [deleteCorrectionSettingId, setDeleteCorrectionSettingId] = useState('')
    const [isShowDeleteCorrectionSettingModal, setIsShowDeleteCorrectionSettingModal] = useState(false)
    const settingTabs = [
        // { name: "Setting", onClickFun: (id) => getSetting(id), id: "setting" },
        { name: "Correction Setting", onClickFun: (id) => getSetting(id), id: "correctionSetting", permissionId:"correction_setting"},
    ];
    const activeSettingTab =activeTabPermission(settingTabs)
    const [allClientData, setAllClientData] = useState('')
    const toggleCorrectionSetting = (data) => {
        if (data) {
            setSelectCorrectionSetting(data)
        } else {
            setSelectCorrectionSetting('')
        }
        setIsOpenCorrectionSetting(!isOpenCorrectionSetting)
    }
    const getSetting = (type) => {
        if (type === "correctionSetting") {
            getAllClientData()
        }

    }

    const getAllClientData = () => {
        dispatch(clientActions.getAllClientRequest())
    }
    /* Get All Client Data  */
    useEffect(() => {
        if (!getAllClientPending) {
            if (getAllClientError) {
                return;
            }
            if (getAllClientRes) {
                if (getAllClientRes?.code == 200) {
                    setAllClientData(getAllClientRes?.data)
                }
            }
        }
    }, [
        getAllClientPending,
        getAllClientRes,
        getAllClientError
    ]);
    const handleAddUpdateCorrectionSetting = (payload) => {
        if (selectedCorrectionSetting) {
            dispatch(clientActions.updateClientRequest(payload))
        } else {
            dispatch(clientActions.addClientRequest(payload))
        }
    }

    /*Add Update Client */
    useEffect(() => {
        if (!addClientPending) {
            if (addClientError) {
                dispatch(clientActions.handleClearClientStore("HANDLE_CLEAR_ADD_CLIENT_STORE"))
                return;
            }
            if (addClientRes) {
                if (addClientRes?.code == 200) {
                    dispatch(clientActions.handleClearClientStore("HANDLE_CLEAR_ADD_CLIENT_STORE"))
                    dispatch(clientActions.handleClearClientStore("HANDLE_CLEAR_GET_ALL_CLIENT_STORE"))
                    toggleCorrectionSetting()
                    getAllClientData()
                }
            }
        }
    }, [
        addClientPending,
        addClientRes,
        addClientError
    ]);
    useEffect(() => {
        if (!updateClientPending) {
            if (updateClientError) {
                dispatch(clientActions.handleClearClientStore("HANDLE_CLEAR_UPDATE_CLIENT_STORE"))
                return;
            }
            if (updateClientRes) {
                if (updateClientRes?.code == 200) {
                    dispatch(clientActions.handleClearClientStore("HANDLE_CLEAR_GET_ALL_CLIENT_STORE"))
                    dispatch(clientActions.handleClearClientStore("HANDLE_CLEAR_UPDATE_CLIENT_STORE"))
                    toggleCorrectionSetting()
                    getAllClientData()
                }
            }
        }
    }, [
        updateClientPending,
        updateClientRes,
        updateClientError
    ]);

    const handleCorrectionSettingDeleteModal = (id) => {
        if (id) {
            setDeleteCorrectionSettingId(id)
        }
        setIsShowDeleteCorrectionSettingModal(!isShowDeleteCorrectionSettingModal)
    }

    const handleDeleteCorrectionSetting = (type) => {
        if (type) {
            const params = {
                _id: deleteCorrectionSettingId
            }

            dispatch(clientActions?.deleteClientRequest(params))

        } else {
            handleCorrectionSettingDeleteModal()
        }

    }

    useEffect(() => {
        if (!deleteClientPending) {
            if (deleteClientError) {
                dispatch(clientActions.handleClearClientStore("HANDLE_CLEAR_DELETE_CLIENT_STORE"))
                return;
            }
            if (deleteClientRes) {
                if (deleteClientRes?.code == 200) {
                    dispatch(clientActions.handleClearClientStore("HANDLE_CLEAR_DELETE_CLIENT_STORE"))
                    handleCorrectionSettingDeleteModal()
                    getAllClientData()
                }
            }
        }
    }, [
        deleteClientPending,
        deleteClientRes,
        deleteClientError
    ]);

    return (

        <div className="cardBox myRequest mb-3">
            <div className="cardHead d-flex align-items-center">
                <img
                    src="images/kpi-guidline.svg"
                    alt=""
                    width={40}
                    className="me-2 fw-bold"
                />
                <h3> Setting</h3>
            </div>
            <CustomTabs tabs={settingTabs} type={"settingTabs"} />
            <div className="tab-content mykpiMain">
                <div className="tab-pane fade " id="setting">

                </div>
                <div className={`tab-pane fade ${activeSettingTab ==="correctionSetting"&&"active show"}`} id="correctionSetting">
                    <CorrectionSetting
                        toggleAddUpdateModal={toggleCorrectionSetting}
                        correctionSettingData={allClientData}
                        isLoading={getAllClientPending}
                        handleCorrectionSettingDeleteModal={handleCorrectionSettingDeleteModal}
                    />
                </div>
            </div>
            {isOpenCorrectionSetting && <AddUpdateCorrectionSetting
                openModel={isOpenCorrectionSetting}
                handleOpenClose={toggleCorrectionSetting}
                handleAddUpdateCorrectionSetting={handleAddUpdateCorrectionSetting}
                selectedCorrectionSetting={selectedCorrectionSetting}
              

            />}
            {isShowDeleteCorrectionSettingModal && (
                <ApproveRejectModal
                    onApproveReject={handleDeleteCorrectionSetting}
                    isShow={isShowDeleteCorrectionSettingModal}
                    desc={t("alerts.deletePopup.desc").replace('$1', "Correction Setting")}
                    btnRightLoading={deleteClientPending}
                    btnTextRight={t("btns.delete")}
                    btnTextLeft={t("btns.cancel")}

                />)
            }
        </div>

    )
}

export default Setting
