import React, { useMemo } from 'react'
import { useCustomTranslation } from '../../../translation'
import { CustomButton } from '../../globalComponents'
import { CustomTable } from '../../reuseableComponents'
import { getBadgeColor, isPermission } from '../../../utils/common'
import { Icon } from '../../commanComponent'

const CorrectionSetting = (props) => {
    const { t } = useCustomTranslation()
    const {
        currentPage,
        setCurrentPage,
        correctionSettingData,
        toggleAddUpdateModal,
        isLoading,
        handleCorrectionSettingDeleteModal
    } = props
    const updateCorrectionSetting =isPermission("update_correction_setting")
    const deleteCorrectionSetting =isPermission("delete_correction_setting")

    const scopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),
        is_active: item => (
            <span
              className={`badge ${getBadgeColor(item.is_active ? "Active" : "Inactive")}`}
            >
              {item.is_active ? "Active" : "Inactive"}
            </span>
          ),
        action: item => (
            <div className="btn-group actionDrp">
                <button type="button" className="btn dropdown-toggle-split"
                    data-bs-toggle="dropdown" aria-expanded="false">
                    <i className="bi bi-three-dots-vertical"></i>
                </button>
                <ul className="dropdown-menu">
                   {updateCorrectionSetting && <li>
                        <span className="dropdown-item"
                            onClick={() => toggleAddUpdateModal(item)}
                        >
                        <Icon className={"bi bi-pencil-square"}/>    Update</span>
                    </li>}
                    {deleteCorrectionSetting &&<li>    
                        <span className="dropdown-item"
                            onClick={() => handleCorrectionSettingDeleteModal(item?.id)}
                        >
                           <Icon className={"bi bi-trash"}/> Delete</span>
                    </li>}
                </ul>
            </div>
        )
    }), [correctionSettingData, currentPage]);

    const fields = [
        { key: 'sn', label: 'S.N' },
        { key: 'client_name', label: 'Client Name' },
        { key: 'attendance_correction_days', label: 'Attendance Correction Days' },
        { key: 'auto_logout_days', label: 'Auto Logout Days' },
        { key: 'attendance_logs_days', label: 'Attendance Logs Days' },
        { key: 'is_active', label: 'Status' },
        ((updateCorrectionSetting ||deleteCorrectionSetting)&&{ key: 'action', label: 'Action' })


    ];
    return (
        <div>
            <div className=' myRequest mb-3 employeeHistory'>
                <div className="d-flex align-items-center justify-content-between gap-3">
                    <div className=" d-flex">
                        {/* <img src="images/myrequest-icon.svg" alt="" width={35} /> */}
                        <h3 className="ms-2 mt-2 underlineHead " style={{ fontSize: "18px" }} >{t("headings.correctionSetting")}</h3>
                    </div>
                        <CustomButton
                            text={"Add Correction Setting"}
                            classNames={"btn btn-primary"}
                            handleClick={() => toggleAddUpdateModal()}
                            slug={"add_correction_setting"}
                        />
                </div>
                <CustomTable
                    data={correctionSettingData?.data}
                    fields={fields}
                    isLoading={isLoading}
                    pagination={true}
                    currentPage={currentPage}
                    setCurrentPage={setCurrentPage}
                    totalPages={correctionSettingData?.totalPages}
                    isHeader={false}
                    tableStyle={{ padding: "0px", boxShadow: "none" }}
                    scopedSlots={scopedSlots}
                />
            </div>
        </div>
    )
}

export default CorrectionSetting
