
import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { validateSchema } from '../../../utils/ValidationHelper'
import { correctionSettingSchema } from '../../../utils/ValidationSchema'
import { CustCheckbox, Input, Label } from '../../commanComponent'
import { CustomButton, CustomError } from '../../globalComponents'
import { useCustomTranslation } from '../../../translation'
import { CommonCKEditor } from '../../reuseableComponents'

const AddUpdateCorrectionSetting = (props) => {
  const { t } = useCustomTranslation()
  const { updateClientPending, addClientPending } = useSelector(state => state.client)
  const { createPermissionPending } = useSelector(state => state?.permission)
  const {
    openModel,
    handleOpenClose,
    handleAddUpdateCorrectionSetting,
    selectedCorrectionSetting


  } = props
  const [errors, setErrors] = useState({})
  const [correctionSettingFormData, setCorrectionSettingFormData] = useState({
    clientCode: '',
    port: '',
    attendanceCorrectionDays: '',
    clientName: '',
    autoLogoutDays: '',
    attendanceImageLogsDays: '',
    attendanceLogsDays: '',
    baseUrl: '',
    isActive: false,
    isAttendanceCorrectionNotification: false,
    notificationText: ''
  })
  const [editorData,setEditorData]=useState('')
  const [bodyData, setBodyData] = useState('')

  const handleFromChange = (e, fieldName) => {
    const name = fieldName || e.target?.name;
    const { value, type, checked } = e.target ? e.target : { value: e };

    setCorrectionSettingFormData(prevState => {
      const updatedState = {
        ...prevState,
        [name]: type === 'checkbox' ? checked : value,
      };
      return updatedState;
    });

    // Reset error for this field
    setErrors(prevErrors => ({
      ...prevErrors,
      [name]: undefined,
    }));
  };

  const submitAddUpdateCorrectionSetting = async () => {
    const validationResponse = await validateSchema(correctionSettingSchema, correctionSettingFormData);
    if (validationResponse.isValid) {
      const {
        clientCode,
        port,
        attendanceCorrectionDays,
        clientName,
        autoLogoutDays,
        attendanceImageLogsDays,
        attendanceLogsDays,
        baseUrl,
        isActive,
        notificationText,
        isAttendanceCorrectionNotification
      } = correctionSettingFormData
      let params = {
        code: clientCode,
        client_name: clientName,
        base_url: baseUrl,
        port: port,
        attendance_correction_days: attendanceCorrectionDays,
        is_attendance_correction_notification: isAttendanceCorrectionNotification,
        attendance_correction_notification: editorData,
        auto_logout_days: autoLogoutDays,
        attendance_image_logs_days: attendanceImageLogsDays,
        attendance_logs_days: attendanceLogsDays,
        is_active: isActive
      }
      params["_id"] = selectedCorrectionSetting?.id
      handleAddUpdateCorrectionSetting(params)
    } else {
      setErrors(validationResponse.errors);
    }
  }

  useEffect(() => {
    return (() => {
      resetForm()
    })
  }, [openModel])

  const resetForm = () => {
    setCorrectionSettingFormData({
      clientCode: '',
      port: '',
      attendanceCorrectionDays: null,
      clientName: '',
      autoLogoutDays: null,
      attendanceImageLogsDays: '',
      attendanceLogsDays: '',
      baseUrl: '',
      isActive: false,
      isAttendanceCorrectionNotification: false,
      notificationText: ''
    })
    setErrors({})
  }

  useEffect(() => {
    if (selectedCorrectionSetting) {
      setCorrectionSettingFormData({
        clientCode: selectedCorrectionSetting?.code || '',
        port: selectedCorrectionSetting?.port,
        attendanceCorrectionDays: selectedCorrectionSetting?.attendance_correction_days,
        clientName: selectedCorrectionSetting?.client_name,
        autoLogoutDays: selectedCorrectionSetting?.auto_logout_days,
        attendanceImageLogsDays: selectedCorrectionSetting?.attendance_image_logs_days,
        attendanceLogsDays: selectedCorrectionSetting?.attendance_logs_days,
        baseUrl: selectedCorrectionSetting?.base_url,
        isActive: selectedCorrectionSetting?.is_active,
       
        isAttendanceCorrectionNotification: selectedCorrectionSetting?.is_attendance_correction_notification
      })
      setEditorData(selectedCorrectionSetting?.attendance_correction_notification)
    }
  }, [selectedCorrectionSetting])

  return (
    <>
      <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
        tabIndex={-1}
        style={{
          visibility: openModel ? 'visible' : 'hidden',
          width: "800px"
        }}
        id="interviewDetail"
      >
        <div className="offcanvas-header">
          <h5 className="offcanvas-title  fs-5 fw-bold text-primary">Correction Setting</h5>
          <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
            onClick={() => handleOpenClose()}
          />
        </div>

        <div className="offcanvas-body myRequest ">
          <div className="formPanel">
            <div className="row">
              <div className="col-md-6">
                <div className="form-group mb-2">
                  <Label text={t("labels.clientCode")} required={true} />
                  <Input
                    type="text"
                    placeholder={t("placeholders.clientCode")}
                    value={correctionSettingFormData?.clientCode}
                    name="clientCode"
                    onChange={(e) => handleFromChange(e)}
                  />
                  {errors?.clientCode && (
                    <CustomError text={errors.clientCode} />
                  )}
                </div>
              </div>
              <div className="col-md-6">
                <div className="form-group mb-2">
                  <Label text={t("labels.clientName")} required={true} />
                  <Input
                    type="text"
                    placeholder={t("placeholders.clientName")}
                    value={correctionSettingFormData?.clientName}
                    name="clientName"
                    onChange={(e) => handleFromChange(e)}
                  />
                   {errors?.clientName && (
                  <CustomError text={errors.clientName} />
                )}
                </div>
               
              </div>
              <div className="col-md-6">
                <div className="form-group mb-2">
                  <Label text={t("labels.baseUrl")} required={true} />
                  <Input
                    type="text"
                    placeholder={t("placeholders.baseUrl")}
                    value={correctionSettingFormData?.baseUrl}
                    name="baseUrl"
                    onChange={(e) => handleFromChange(e)}
                  />
                  {errors?.baseUrl && (
                  <CustomError text={errors.baseUrl} />
                )}
                </div>
               
              </div>


              <div className="col-md-6">
                <div className="form-group mb-2">
                  <Label text={t("labels.port")} required={true} />
                  <Input
                    type="number"
                    placeholder={t("placeholders.port")}
                    value={correctionSettingFormData?.port}
                    name="port"
                    onChange={(e) => handleFromChange(e)}
                  />
                  {errors?.port && (
                  <CustomError text={errors.port} />
                )}
                </div>
                
              </div>
              <div className="col-md-6">
                <div className="form-group mb-2">
                  <Label text={t("labels.attendanceCorrectionDays")} required={true} />
                  <Input
                    type="number"
                    placeholder={t("placeholders.attendanceCorrectionDays")}
                    value={correctionSettingFormData?.attendanceCorrectionDays}
                    name="attendanceCorrectionDays"
                    onChange={(e) => handleFromChange(e)}
                  />
                  {errors?.attendanceCorrectionDays && (
                  <CustomError text={errors.attendanceCorrectionDays} />
                )}
                </div>
                
              </div>
              <div className="col-md-6">
                <div className="form-group mb-2">
                  <Label text={t("labels.autoLogoutDays")} required={true} />
                  <Input
                    type="number"
                    placeholder={t("placeholders.autoLogoutDays")}
                    value={correctionSettingFormData?.autoLogoutDays}
                    name="autoLogoutDays"
                    onChange={(e) => handleFromChange(e)}
                  />
                </div>
                {errors?.autoLogoutDays && (
                  <CustomError text={errors.autoLogoutDays} />
                )}
              </div>
              <div className="col-md-6">
                <div className="form-group mb-2">
                  <Label text={t("labels.attendanceImageLogsDays")} />
                  <Input
                    type="number"
                    placeholder={t("placeholders.attendanceImageLogsDays")}
                    value={correctionSettingFormData?.attendanceImageLogsDays}
                    name="attendanceImageLogsDays"
                    onChange={(e) => handleFromChange(e)}
                  />
                  {errors?.attendanceImageLogsDays && (
                  <CustomError text={errors.attendanceImageLogsDays} />
                )}
                </div>
                
              </div>
              <div className="col-md-6">
                <div className="form-group mb-2">
                  <Label text={t("labels.attendanceLogsDays")} />
                  <Input
                    type="number"
                    placeholder={t("placeholders.attendanceLogsDays")}
                    value={correctionSettingFormData?.attendanceLogsDays}
                    name="attendanceLogsDays"
                    onChange={(e) => handleFromChange(e)}
                  />
                  {errors?.attendanceLogsDays && (
                  <CustomError text={errors.attendanceLogsDays} />
                )}
                </div>
                
              </div>
              <div className="row col-md-12">
                <div className="col-md-3">
                  <div className="form-group mb-3">
                    <Label text={t("labels.isAttendanceCorrectionNotification")} />
                  </div>
                </div>
                <div className="col-md-9">
                  <div className="form-group mb-3">
                    <CustCheckbox
                      key={"isAttendanceCorrectionNotification"}
                      id={`isAttendanceCorrectionNotification`}
                      // label={"Active"}
                      checked={correctionSettingFormData?.isAttendanceCorrectionNotification}
                      handleChange={(e) => handleFromChange(e)}
                      name={"isAttendanceCorrectionNotification"}
                    />

                  </div>
                </div>
              </div>
              {<div className="row col-md-12">
                
                  <div className="form-group mb-2">
                    <Label text={t("labels.notificationText")} />
                    <CommonCKEditor
                      editorData={editorData}
                      setEditorData={setEditorData}
                      bodyData={bodyData}
                      editorSize="sm"
                    />

                    {/* <Input
                      type="number"
                      placeholder={t("placeholders.notificationText")}
                      value={correctionSettingFormData?.notificationText}
                      name="notificationText"
                      onChange={(e) => handleFromChange(e)}
                    /> */}
                  </div>
               
              </div>}
              <div className="row col-md-12">
                <div className="col-md-3">
                  <div className="form-group mb-3">
                    <Label text={t("labels.isActive")} />
                  </div>
                </div>
                <div className="col-md-9">
                  <div className="form-group mb-3">
                    <CustCheckbox
                      key={"isActive"}
                      id={`isActive`}
                      // label={"Active"}
                      checked={correctionSettingFormData?.isActive}
                      handleChange={(e) => handleFromChange(e)}
                      name={"isActive"}
                    />

                  </div>
                </div>
              </div>

            </div>

          </div>
        </div>
        <div className="offcanvas-footer">
          <div className="d-flex align-items-center justify-content-end gap-3">
            <CustomButton
              text={selectedCorrectionSetting ? t("btns.update") : t("btns.submit")}
              classNames={"btn btn-org-gradient"}
              handleClick={() => submitAddUpdateCorrectionSetting()}
              isLoading={selectedCorrectionSetting ? updateClientPending : addClientPending}
            />
          </div>
        </div>
      </div>
      {openModel && <div className="modal-backdrop fade show"></div>}
    </>
  )
}

export default AddUpdateCorrectionSetting
